.card{
    margin: 2rem 2rem 2rem 2rem;
    Padding: 1.5rem;
    width: 280px;
    box-shadow: 0 0 28px 4px rgba(0,0,0,0.1);
    transition: transform .2s ease;
    cursor: pointer;
}

.card:hover{
    transform: scale(1.1);
}

.card_titile{
    font-weight: 1000;
    font-size: 1.5rem;
}

.card_image{
    display: flex;
    justify-content: flex-end;
}

.card_image img{
    border-radius: 50%;
    
}